@import url(https://cdn.shopify.com/shopify-marketing_assets/builds/30.6.0/marketing_assets/builds/shopify-sans.css);
.Polaris-Page-Header {
    padding-top: 0;
}

.game-dashboard {
    padding-top: 3.2rem;
}

.shadowRealm {
    display: flex;
    flex-wrap: wrap;
}

.avatar {
    width: 48px;
    height: 48px;
    margin: 8px;
    border-radius: 50%;
}

.limbo {
    display: flex;
    flex-wrap: wrap;
}

.Polaris-Card {
    background-color: rgb(43, 53, 63);
}

.Polaris-DataTable__Table {
    border-collapse: collapse;
}

.Polaris-DataTable__Cell--header {
    color: #F4F6F8;
}

.Polaris-DataTable__Cell {
    border: solid 1px rgb(66, 73, 82);
    padding: 0;
}

.Polaris-DataTable__Cell--firstColumn {
    color: inherit;
}

.Polaris-DataTable__TableRow:hover .Polaris-DataTable__Cell {
    background-color: #212B35;
}

thead {
    display: none;
}

th {
    height: 52px;
    width: 52px;
}

tr {
    height: 52px;
}

td {
    position: relative;
}

.record {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

.Polaris-DataTable__Cell--firstColumn + td {
    border-right: solid 2px rgb(87, 96, 109);
    width: 100px;
}

.Polaris-DataTable__Cell {
    text-align: center;
    vertical-align: inherit;
}

.record--worst {
    background-color: rgba(180, 0, 0, 0.2);
}

.record--best {
    background-color: rgba(0, 180, 0, 0.2);
}

.record--value {
    padding: 4px;
}

body {
    font-family: ShopifySans, Arial, sans-serif !important;
    background-color: #212B35;
    color: #F4F6F8;
    margin: 0px;
    margin-top: 8px;
    /* overflow: hidden; */
}

.Polaris-Banner {
    color: black;
}

